.cookiealert {
  font-size: 14px;
  line-height: 1.5;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  margin: 0 !important;
  z-index: 999;
  opacity: 0;
  border-radius: 0;
  transform: translateY(100%);
  transition: all 500ms ease-out;
  color: #ecf0f1;
  background: #212327 url("data:image/png;base64,");
}

.cookiealert.show {
  opacity: 1;
  transform: translateY(0%);
  transition-delay: 1000ms;
}

.cookiealert a {
  text-decoration: underline !important;
  color: #781049;
}
.cookiealert a:hover {
  text-decoration: none !important;
}

