  font-size: 18px;
  list-style-type: none;
  margin-top: 1.25rem;
  font-family: var(--font-secondary, "Domine", serif);
  font-weight: 400;
}

ol.list-numbers li::before {
  content: counter(item) ". ";
  counter-increment: item;
  margin-right: 7px;
  color: var(--themecolor, #267DF4);
  font-weight: 700;
}